body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font-family: "Comic Sans MS";
  height: 100%;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 15s linear;
          animation: App-logo-spin infinite 15s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.social-media {
  text-align: justify;
  opacity: 0;
  background: lightyellow;
  position: absolute;
  top: 0;
  right: 0;
  width: 20em;
  padding: 1em 1em 2em;
  -webkit-transition: .5s;
  transition: .5s;
  z-index: 2;
}

.social-media:hover {
  opacity: 1;
}

.content {
  position: absolute;
  top: 0;
  width: 100%
}

.background {
  opacity: 0.9;
  background-color: #000;
  position: absolute;
  top: 0;
  width: 100%;
  height: 118.5%;
}

.foreground {
  opacity: 0;
  position: absolute;
  top: 0;
  width: 100%;
  height: 118.5%;
  z-index: 1;
}

.video-wrapper {
  position: relative;
  padding-bottom: 56%; /* 16:9 */
  padding-top: 25px;
}

.video-wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.version{
  color: white;
  position: absolute;
  bottom: 0;
  right: 0;
}

@media screen and (max-width: 1055px){
  .video-wrapper {
    display: none;
  }
}

